<template>
  <div>
    <div class="center-home-main">
      <div class="user-head">
        <div class="demo-image__preview head-img" >
          <el-image
            style="width: 100px; height: 100px;margin-top: 0px;border-radius: 50%;"
            :src="resUserInfo.userInfo.bpic"
          >
          </el-image>
        </div>
        <div class="user-info-head">
          <span class="user-name" style="margin: 5px 10px;font-size: 25px">{{resUserInfo.userInfo.bname}}</span>


            <el-button type="danger" id="back"  size="small" icon="el-icon-delete" circle></el-button>
            <el-badge :value="resUserInfo.userInfo.bmsg" class="item">
              <el-button  size="small" type="info" icon="el-icon-message" circle></el-button>
            </el-badge>

          <span style="font-size: 15px">
            <i class="el-icon-star-off">粉丝 {{resUserInfo.userInfo.bfans}} 个&nbsp;&nbsp;</i>
            <i class="el-icon-connection">关注 {{resUserInfo.userInfo.bfocus}} 个</i></span>
          <el-button class="submit-login" size="small" style="float: right;position: relative;left: 100px">修改个人信息</el-button>
          <br/>
         <span style="font-size: 10px;margin-left: 10px">上一次登录时间 {{resUserInfo.userInfo.lasttime}}</span>
        </div>
      </div>
      <div class="user-info">
        <el-collapse accordion>
          <el-collapse-item  title="个性标签">
<!--  需要设置字数和个数上限 ，存的时候在数据库用分割符隔开存，然后再解析-->
            <el-tag
              :key="tag"
              v-for="tag in resUserInfo.userInfo.btags"
              closable
              :disable-transitions="false"
              @close="handleClose(tag)">
              {{tag}}
            </el-tag>
            <el-input
              class="input-new-tag"
              v-if="inputVisible"
              v-model="inputValue"
              ref="saveTagInput"
              size="small"
              @keyup.enter.native="handleInputConfirm"
              @blur="handleInputConfirm"
            >
            </el-input>
            <el-button v-else class="button-new-tag" size="small" @click="showInput">+ 添加</el-button>
          </el-collapse-item>
          <el-collapse-item title="实名制">
              <div>
                <el-button v-if="resUserInfo.userInfo.breal==0?true:false" size="small" type="danger">去实名制</el-button>
                <el-button v-if="resUserInfo.userInfo.breal==0?false:true" size="small" type="success">已实名制</el-button>
              </div>

          </el-collapse-item>
          <el-collapse-item title="信誉分">
            <el-rate
              v-model="resUserInfo.userInfo.bcredit"
              disabled
              show-score
              text-color="#ff9900"
              score-template="{value}">
            </el-rate>
          </el-collapse-item>
          <el-collapse-item title="积分">
            <i class="el-icon-coin">{{resUserInfo.userInfo.bscore}}</i>
          </el-collapse-item>
          <el-collapse-item title="个人简介">
            <div v-text="resUserInfo.userInfo.bqianming"> </div>
          </el-collapse-item>
        </el-collapse>
      </div>
    </div>
    <div class="center-home-right">

      <el-upload
        style="margin-top: -20px"
        class="upload-demo"
        drag
        action="https://jsonplaceholder.typicode.com/posts/"
        multiple>
        <i class="el-icon-upload"></i>
        <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
        <div class="el-upload__tip" slot="tip">只能上传jpg/png文件，且不超过500kb</div>
      </el-upload>
      <el-input
        style="width: 150px"
        placeholder="新用户名"
        prefix-icon="el-icon-user-solid"
        v-model="newName">
      </el-input>
    </div>

  </div>
</template>

<script>
import Axios from "axios";
import {mapState} from 'vuex'
export default {
  name: "CenterHome",
  data(){
    return{
      dynamicTags: ['标签一', '标签二', '标签三'],
      inputVisible: false,
      inputValue:'',
      value: 2,
      newName:''
    }
  },
  computed:{
    ...mapState(['resUserInfo'])
  },
  methods:{
    handleClose(tag) {
      this.resUserInfo.userInfo.btags.splice(this.resUserInfo.userInfo.btags.indexOf(tag), 1);
    },
    showInput() {
      if (this.userInfo.btags.length>5){
        this.$message.error("最多只能设置6个个性标签")
        return;
      }
      this.inputVisible = true;
      this.$nextTick(_ => {
        this.$refs.saveTagInput.$refs.input.focus();
      });
    },

    handleInputConfirm() {
      let inputValue = this.inputValue;
      if (inputValue) {
        this.resUserInfo.userInfo.btags.push(inputValue);
      }
      this.inputVisible = false;
      this.inputValue = '';
    }
  },
  created() {
   this.$store.dispatch('reFashUserInfo');
  }
}
</script>

<style scoped>
  .user-info{
    position: relative;
    float: left;
      width: 600px;
  }
  .center-home-right{
    float: right;
    width: 500px;
    height: 250px;
    background: #5daf34;
  }
  .user-head{
    position: relative;
    width: 500px;
    height: 100px;
  }
  .user-info-head{
    width: 500px;
    padding-top:40px;
  }
  .head-img{
    float: left;
    width: 100px;
    height: 100px;
  }
  .item{
    position: absolute;
    top: 10px;
    right: -100px;
  }
  #back{
    position: absolute;
    top: 10px;
    right: -60px;
  }
  .el-tag + .el-tag {
    margin-left: 10px;
  }
  .button-new-tag {
    margin-left: 10px;
    height: 32px;
    line-height: 30px;
    padding-top: 0;
    padding-bottom: 0;
  }
  .input-new-tag {
    width: 90px;
    margin-left: 10px;
    vertical-align: bottom;
  }
</style>



