import Vue from 'vue'
import Router from 'vue-router'
import Login from "../views/Login";
import Home from "../views/Home";
import Center from "../views/Center";
import Goods from "../views/Goods";
import DefaultGoods from "../views/DefaultGoods";
import SouSuo from "../views/SouSuo";
import Register from "../views/Register";
import Wish from "../views/Wish";
import Notice from "../views/Notice";
import OtherCenter from "../views/OtherCenter";

Vue.use(Router);

export default new Router({

  routes: [
    {
      path: '/Login',
      name: 'Login',
      component: Login
    },
    {
      path: '/LoginOut',
      redirect: Login
    },
    {
      path: '/Register',
      name: 'Register',
      component: Register
    },
    {
      path: "/SouSuo",
      name: "SouSuo",
      component: SouSuo
    },
    {
      path: '/Goods',
      name: 'Goods',
      component: Goods
    },
    {
      path: '/Wish',
      name: 'Wish',
      component: Wish
    },
    {
      path: '/Notice',
      name: 'Notice',
      component: Notice
    },
    {
      path: '/Home',
      name:'Home',
      component: Home,
      children:[
        {
          path:"/Home",
          name:"DefaultGoodsGoods",
          component:DefaultGoods
        },
        {
          path: "Login",
          redirect:"/Login"
        },
        {
          path: "/SouSuo",
          redirect: "/SouSuo"
        },
        {
          path:"/Goods",
          redirect: "/Goods"
        }
      ]
    },
    {
      path: '/Center',
      name: 'Center',
      component: Center
    },
    {
      path: '/OtherCenter/:id',
      name: 'OtherCenter',
      component: OtherCenter
    }

  ]
})



