        <div slot="header" class="clearfix">
          <span style="color: plum">今日运势</span>
          <el-button @click="reFl" style="float: right; padding: 3px 0" type="text"></el-button>
        </div>
        <div v-for="(m,index) in msg" style="padding: 5px 0 " class="text item">
          <span style="color: plum">{{index+1}}</span> {{m}}
        </div>
      </el-card>
    </el-col>
  </el-row>
</template>

<script>
export default {
  name: "YunShi",
  data(){
    return{
      msg:[]
    }
  }
}
</script>

