<template>
  <el-container>
    <el-header height="100px">
      <MyHeader></MyHeader>
    </el-header>
    <el-container>
      <el-aside width="202px">
        <el-radio-group v-model="isCollapse" style="margin-bottom: 20px;">
          <el-radio-button  :label="false">Open</el-radio-button>
          <el-radio-button :label="true">Close</el-radio-button>
        </el-radio-group>
        <el-menu default-active="1" class="el-menu-vertical-demo" @open="handleOpen" @close="handleClose" :collapse="isCollapse">
<!--          把后台获得的类型获取过来             -->
          <el-menu-item v-for="item in typeList" :key="item.typeid">
            <i class="el-icon-menu"></i>
            <span slot="title" @click="loadGoods(item)">{{item.typename}}</span>
          </el-menu-item>

        </el-menu>
      </el-aside>
      <el-main>
        <el-row>
<!--          点击查看商品详情后会弹出的界面-->
          <el-dialog
            title="商品详情"
            :visible.sync="dialogVisible"
            width="50%"
            :before-close="handleClose2">
            <div class="inner-goods">
                <div class="inner-goods-pic">
                      <img src="static/xiandan.png" alt="找不到图片">
                </div>
                <div class="inner-goods-content">
                    <table>
                      <tr>
                        <td>商品名:<span>{goodsname}</span></td>
                        <td>价格:<span>{price}</span>元</td>
                      </tr>
                      <tr>
                        <td>上架时间:{time}</td>
                      </tr>
                      <tr>
                        <td colspan="2">描述：</td>
                      </tr>
                      <tr>
                        <td colspan="2" rowspan="2"><p>
                          这是一个神奇的东西这是一个神奇的东西这是一个神奇的东西这是一个神奇的东西这是一个神奇的东西这是一个神奇的东西这是一个神奇的东西
                        </p></td>
                      </tr>
                    </table>

                </div>
            </div>
            <span slot="footer" class="dialog-footer">
                <el-button type="primary" @click="golooklook">去看看</el-button>
                 <el-button type="primary" @click="dialogVisible = false">返回</el-button>
            </span>
          </el-dialog>

        </el-row>
        <el-row>
          <el-col :span="23" :offset="1"><div class="grid-content bg-purple-dark"><el-divider></el-divider></div></el-col>
        </el-row>
        <el-row>
          <el-col :span="5" style="margin-bottom: 50px;" v-for="(o, index) in 16" :key="o" :offset="1">
            <el-card style="padding: 0 10px;margin: 0 ">
              <img src="static/photo/xiandan.png" class="image">
              <div style="padding: 14px;">
                <span>xiandian</span><br/>
                <span>100元</span>
                <div class="bottom clearfix">
                  <el-button type="text" class="button" @click="look(o)">查看详情</el-button>
                </div>
              </div>
            </el-card>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="24" align="center">
            <el-pagination
              background
              @current-change="changePage"
              layout="prev, pager, next"
              :page-count="page"
              >
            </el-pagination>
          </el-col>
        </el-row>
      </el-main>
    </el-container>
  </el-container>

</template>

<script>
import Axios from "axios";

export default {
  name: "Goods",
  data(){
    return{
      "count":"20",
      busy:false,
      goodstypeid:"",
      type:"",
      page:3,
      pageSize: 8,
      Goods:"",
      dialogVisible: false,
      thegoodpostsid:111,
      typeList:[
        {
          "typeid":1001,
          "typename":"书籍"
        },
        {
          "typeid":1002,
          "typename":"电子产品"
        },
        {
          "typeid":1003,
          "typename":"生活用品"
        },
        {
          "typeid":1004,
          "typename":"小物件"
        },
        {
          "typeid":1005,
          "typename":"其他"
        }

      ],
      isCollapse: true,
      loading: false
    }
  },methods: {

    loadGoods (goods) {
      this.page=1;
      this.goodstypeid=goods.typeid;
      let that = this;
    //  根据商品类型发请求,获取此类型的页码和第一页的商品
      Axios({
          method:"get",
          url:"http://xxxxxx?tyorid="+that.goodstypeid+"&page="+that.page+"&pageSize="+this.pageSize
      }).then(function (res) {
        that.page=res.data;
        that.Goods=res.data;
      })
    },
    changePage(){
      let that = this;
      //  根据商品类型发请求,获取此类型的页码和第一页的商品
      Axios({
        method:"get",
        url:"http://xxxxxx?tyorid="+this.goodstypeid+"&page="+that.page+"&pageSize="+this.pageSize
      }).then(function (res) {
        that.page=res.data;
        that.Goods=res.data;
      })
      console.log("商品类型"+this.goodstypeid+"页码："+this.page+"一页"+this.pageSize+"个商品")
    },
    handleOpen(key, keyPath) {
      console.log(key, keyPath);
    },
    handleClose(key, keyPath) {
      console.log(key, keyPath);
    },
    handleClose2(done) {
      this.$confirm('确认关闭？')
        .then(_ => {
          done();
        })
        .catch(_ => {});
    },
    look(goods){
      console.log(goods);
      //把这个商品的主人的id保存,便于以后的操作
      // this.thegoodpostsid=goods.guserid;
      this.dialogVisible=true;
    },
    golooklook(){
        this.$router.push("/OtherCenter/"+this.thegoodpostsid)
    }
  },
  created() {
    //获取商品的类型,并返回第一个类型第一页的商品信息
    let that = this;
      Axios({
        method:"get",
        url:"http://xxxxxxx"
      }).then(function (res) {
        that.typeList=res.data;
        // that.goodstypeid=10001;
        that.Goods=res.data;
      })
  }
}
</script>

<style scoped>
ul{
  list-style: none;
}
li{
  display: block;
}
.el-menu-vertical-demo:not(.el-menu--collapse) {
  width: 200px;
  min-height: 400px;
}

.bottom {
  margin-top: 13px;
  line-height: 12px;
}

.button {
  padding: 0;
  float: right;
}

.image {
  width: 100%;
  display: block;
}

.clearfix:before,
.clearfix:after {
  display: table;
  content: "";
}

.clearfix:after {
  clear: both
}
.text {
  font-size: 14px;
}

.item {
  margin-bottom: 18px;
}

.inner-goods{

  width: 100%;
  height: 300px;

}
.inner-goods-pic{
  float: left;
  width: 40%;
  height: 300px;
}
.inner-goods-pic img{
  width: 100%;
  height: 300px;
}
.inner-goods-content{
  float: right;
  width: 60%;
  height: 300px;
}
.inner-goods-content table{
  margin-top: 20px;
  margin-left: 30px;
  width: 80%;
  height: 180px;
}
.inner-goods-content table p{
  color: #409EFF;
}
.inner-goods-content table span{
  font-size: 25px;
  color: #409EFF;
}

</style>



