<template>
  <el-container>
    <el-header height="80px"></el-header>

    <el-main><div class="login-bx">
      <el-row>
        <el-col :span="14"><div class="grid-content bg-purple login-img">
          <img src="static/photo/xiandan.png">
        </div></el-col>
        <el-col :span="10"><div class="grid-content bg-purple login-form">
          <div>
            <span  class="login-head1-text">登录页面</span><br/>
          </div>
          <el-form :model="ruleForm" status-icon :rules="rules"  ref="ruleForm" label-width="98px" class="demo-ruleForm">
            <el-form-item  label="用户名" prop="username">
              <el-input type="text" v-model="ruleForm.username" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item  label="密码" prop="password">
              <el-input type="password" v-model="ruleForm.password" autocomplete="off"></el-input>
            </el-form-item>
            <el-row>
              <el-col :span="8" align="right">
                <img style="width: 105px; height: 45px" :src="verifyImg">
              </el-col>
              <el-col :span="8" >
                <el-link type="primary" style="margin-top: 20px" @click="changeVerifyImg">看不清？换一张</el-link>
              </el-col>
              <el-col :span="8" >
                <el-input
                  placeholder="请输入验证码"
                  prefix-icon="el-icon-unlock"
                  v-model="verifyCode">
                </el-input>
              </el-col>
            </el-row>
            <el-form-item style="margin-top: 20px">
              <el-button type="primary" @click="submitForm('ruleForm')" class="submit-login">登录</el-button>
            </el-form-item>
            <router-link to="Home" style="position: absolute;right: 400px">游客观看</router-link>
            <router-link to="Register" style="position: absolute;right: 10px">没有账号？去注册</router-link>

          </el-form>
        </div></el-col>
      </el-row>


    </div></el-main>
  </el-container>

</template>

<script>

import Axios from "axios";


export default {
  name: "Login",
  data() {
    var rg1 =/^[\u4E00-\u9FA5A-Za-z0-9_]+$/;
    var rg2 =/^[a-zA-Z]\w{5,17}$/;
    var checkName = (rule, value, callback) => {
      if (value==='') {
        return callback(new Error('用户名不能为空'));
      }
      setTimeout(() => {
        if(!rg1.test(value)){
          return callback(new Error('用户名中有非法字符'));
        }
        if (value.length < 1||value.length>10) {
          callback(new Error('用户名必须在1~10个字符之间'));
        } else {
          callback();
        }
      }, 1000);
    };
    var validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入密码'));
      } else {
        if (!rg2.test(value)){
          callback(new Error('以字母开头，长度在6~18之间，只能包含字母、数字和下划线'));
        }
        callback();
      }
    };
    return {
      labelPosition: 'right',
      ruleForm: {
        username:'',
        password: ''
      },
      rules: {
        password: [
          { validator: validatePass, trigger: 'blur' }
        ],
        username: [
          { validator: checkName, trigger: 'blur' }
        ]
      },
      verifyCode:"",
      verifyImg:"",
    };

  },

  methods: {
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          let that=this;
          Axios({
            method:"post",
            url:"http://localhost:8090/login",
            params:{
              "bname":that.ruleForm["username"],
              "bpsd":that.ruleForm["password"],
              "YZM":that.verifyCode
            }
          }).then(function (res) {
              if (res.data.code=='200'){
                that.$message.success("登录成功！")
                that.$router.push("/Home")
              }else if (res.data.code=='400'){
                console.log(res.data.msg)
                that.$message.error(res.data.msg)
                that.changeVerifyImg();
              }
          })
        } else {
          this.$message({
            message: '用户名和密码不合法',
            center: true
          });
          return false;
        }
      });
    },
    changeVerifyImg(){
      let that=this;
      //更改验证码
      Axios({
        method:"get",
        url:"http://localhost:8090/getVerify"
      }).then(function (res) {
        that.verifyImg=res.data;
      })
    }
  },
  created() {
    let  that = this;
    Axios({
        method:"get",
        url:"http://localhost:8090/getVerify",
    }).then(function (res) {

      that.verifyImg=res.data;

    })
  }
}
</script>

<style scoped>
*{
  margin: 0;
  padding:0;
}
.login-head1-text{
  position: relative;
  left: 45px;
  font-size: 30px;
  line-height: 80px;
}
.login-bx{
  position: relative;
  margin: 0 auto;
  border-radius: 4px;
  box-shadow: 0 2px 4px #ace0f9, 0 0 6px #517fa4;
  height: 600px;
  background:linear-gradient(#243949,#517fa4);
  background:linear-gradient(#fff1eb,#ace0f9);
  opacity:0.7;
}
.submit-login{
  width: 100px;
  height: 50px;
}
h2{
  margin-bottom: 20px;
}
.login-img{
  padding: 100px 100px;
  height: 400px;
}
.login-img img{
  width: 100%;
  height: 100%;
}
.login-form{
  margin-top: 20px;
  padding: 120px 50px 100px 0;
  height: 400px;
}
.el-form-item{
  margin-bottom: 20px;
}
.el-main {
  text-align: center;
}
.el-row {
}
.el-row:last-child {
  margin-bottom: 0;
}

.bg-purple {

}
.bg-purple-light {

}
.grid-content {
  border-radius: 4px;
  min-height: 36px;
}
.row-bg {
}
</style>



