<template>
  <div class="Center-main">
    <el-page-header @back="goBack" style="padding: 20px 0" content="他的主页">
    </el-page-header>
    <div>
      <el-tabs type="border-card">
        <el-tab-pane label="个人主页">
          路由传过来的id是{{id}}
        </el-tab-pane>
        <el-tab-pane label="上架商品">
          上架商品界面
        </el-tab-pane>
        <el-tab-pane label="审核中">
          审核中界面
        </el-tab-pane>
        <el-tab-pane label="愿望">
          愿望界面
        </el-tab-pane>
        <el-tab-pane label="历史记录">
          历史记录界面
        </el-tab-pane>
        <el-tab-pane label="反馈建议">
          反馈建议界面
        </el-tab-pane>
        <el-tab-pane label="更改密码">
          更改密码界面
        </el-tab-pane>
      </el-tabs>
    </div>

    <el-divider></el-divider>

  </div>
</template>

<script>
import CenterHead from "../components/CenterHead";
import CenterHome from "../components/CenterHome";
export default {
  name: "Center",
  data(){
    return{
      id:this.$route.params.id
    }
  },
  methods:{
    goBack() {
      this.$router.push("/Home");
    }
  },
  components: {CenterHome, CenterHead},
  props:[CenterHead,CenterHome]
}
</script>

<style scoped>
.Center-main{

}
</style>



