<template>
  <el-container>
    <el-header height="100px">
      <el-row>
        <el-col :span="8"><div class="grid-content bg-purple">
          <img src="static/photo/xiandan.png" height="100">
        </div></el-col>
        <el-col :span="8"><div class="grid-content bg-purple-light">
          <el-menu :default-active="activeIndex" class="el-menu-demo" mode="horizontal" @select="handleSelect">
            <el-menu-item index="1">首页</el-menu-item>
            <el-menu-item index="2">许愿池</el-menu-item>
            <el-menu-item index="3">新鲜事儿</el-menu-item>
            <el-menu-item index="4">个人中心</el-menu-item>
          </el-menu>
          <div class="line"></div>
        </div></el-col>
        <el-col :span="8"><div class="grid-content bg-purple" align="right">
          <el-row>
            <el-col v-show="loginStatus()" :span="24" style="height: 40px">
              <el-button type="text" ><router-link to="Login">登录</router-link>
              </el-button><el-button type="text"><router-link to="Register">注册</router-link></el-button></el-col>
            <el-col v-show="!loginStatus()" :span="24" style="height: 35px;margin-top:-10px;margin-bottom:20px;color: palevioletred;font-size: 15px">
              <span>欢迎&nbsp;{{resUserInfo.userInfo.bname}}</span></el-col>
          </el-row>
            <el-row style="margin-top: -20px">
              <el-col :span="24" @click="OnSouSuo"><div class="grid-content bg-purple">
                  <el-col :span="18"><div class="grid-content bg-purple">
                    <el-input
                      @focus="OnSouSuo"
                      size="small"
                      placeholder="请输入内容"
                      v-model="input"
                      clearable>
                    </el-input>
                  </div></el-col>
                  <el-col :span="6"><div class="grid-content bg-purple-light">
                    <el-button type="primary" size="small" icon="el-icon-search">搜索</el-button>
                  </div></el-col>
              </div></el-col>
          </el-row>
        </div></el-col>
      </el-row>
    </el-header>
    <el-main>
      <el-row>
        <div class="block">
          <el-carousel height="350px">
            <el-carousel-item v-for="item in bannerList" :key="item">
              <img :src="item" width="100%" >
            </el-carousel-item>
          </el-carousel>
        </div>
      </el-row>
      <el-row>

        <el-col  :span="24"><div class="grid-content bg-purple">
          <el-row>
            <el-col :span="12" align="left">
              <span style="color: blueviolet;font-size: 22px">Hot</span>
            </el-col>
            <el-col :span="12"  align="right"><div class="grid-content bg-purple">
              <el-button type="text" @click="OnGoodsList" ><i class="el-icon-s-unfold"></i>更多商品</el-button>
            </div></el-col>
          </el-row>
        </div></el-col>
      </el-row>
      <el-row>
        <el-col :span="24"><div class="grid-content bg-purple-dark">
            <router-view/>
        </div></el-col>
      </el-row>

      <MyNews></MyNews>
      <MyHistory></MyHistory>
      <MyYunShi></MyYunShi>
    </el-main>
    <el-footer height="150px">Footer</el-footer>
  </el-container>

</template>

<script>
import 'element-ui/lib/theme-chalk/display.css';
import {mapState} from 'vuex'
export default {
  name: "Home",
  data() {
    return {
      flag:true,
      input:"",
      typeList:["书籍","手机","外设","服饰"],
      bannerList:['/static/photo/b1.jpg',
        '/static/photo/b2.jpg',
        '/static/photo/b3.jpg',
        '/static/photo/b4.png',
        '/static/photo/b5.jpg',
        '/static/photo/b6.jpg',
      '/static/photo/b7.jpg'],
      activeIndex: '1',
      activeIndex2: '1'
    };
  },
  computed:{
    ...mapState(['resUserInfo']),
  },
  methods:{

    OnGoodsList(){
      this.$router.push("/Goods");
    },
    OnSouSuo(){
        this.$router.push("/SouSuo");
    },
    handleSelect(key, keyPath) {
      if(key==1){
          this.$router.push("/Home");
      }else if(key==2){
           this.$router.push("/Wish");
      }
      else if(key==3){
        this.$router.push("/Notice");
      }
      else if(key==4){
          this.$router.push("/Center");
      }
    },
    loginStatus(){

      if (this.$store.state.resUserInfo.code==400){
          return true;
      }else{
          return false;
      }
    }
  },
  created() {
     //  发送请求验证是否登录
    this.$store.dispatch('reFashUserInfo');
  }
}
</script>

<style scoped>

.el-header, .el-footer {
  color: #333;
  padding: 0;
  line-height: 60px;
}
.el-main {

  color: #333;
  text-align: center;
  padding: 0;
}
body > .el-container {
  margin-bottom: 40px;
}
.el-menu{
  margin-top: 40px;
}
.el-carousel__item h3 {

  font-size: 14px;
  opacity: 0.75;
  line-height: 150px;
  margin: 0;
}

.el-row {
  margin-bottom: 20px;
}
.el-row :last-child {
  margin-bottom: 0;
}
.el-col {
  border-radius: 4px;
}
.bg-purple-dark {

}
.bg-purple {

}
.bg-purple-light {

}
.grid-content {
  border-radius: 4px;
  min-height: 36px;
}
.row-bg {
  padding: 10px 0;
  background-color: #f9fafc;
}
</style>



