<template>
  <div>
    <el-row>
      <el-col :span="5" v-for="(o, index) in 4" :key="o" :offset="index > 0? 1: 0">
        <el-card style="padding: 0 10px;margin: 0 ">
          <img :src="Goods[index].gpicture" class="image">
          <div style="padding: 14px;">
            <span>{{Goods[index].gname}}</span><br/>
            <span>{{Goods[index].grprice}}元</span>
            <div class="bottom clearfix">
              <el-button type="text" class="button">查看详情</el-button>
            </div>
          </div>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import Axios from "axios";

export default {
  name: "Goods",
  data(){
    return{
    "Goods":{},

  }
  },
  created() {
    let that=this;
    Axios({
      method:"get",
      url:"http://localhost:8090/testDeftGoods",
    }).then(function (res) {
      that.Goods=res.data;
      console.log(res.data);
    })

  }


}
</script>

<style scoped>


.bottom {
  margin-top: 13px;
  line-height: 12px;
}

.button {
  padding: 0;
  float: right;
}

.image {
  width: 100%;
  display: block;
}

.clearfix:before,
.clearfix:after {
  display: table;
  content: "";
}

.clearfix:after {
  clear: both
}
</style>



