<template>
  <el-container>
    <el-header height="100px">
      <el-row>
        <el-col :span="24" style="padding: 10px 0">
          <el-page-header @back="goBack" content="搜索商品">
          </el-page-header>
        </el-col>
        <el-col :span="4"><div class="grid-content bg-purple">
          <img src="static/xiandan.png" height="100">
        </div></el-col>
        <el-col :span="20"><div class="grid-content bg-purple" align="right">
          <el-row>
            <el-col :span="24" style="height: 40px"></el-col>
          </el-row>
          <el-row style="margin-top: -20px">
            <el-col :span="24"><div class="grid-content bg-purple">
              <el-col :span="18"><div class="grid-content bg-purple">
                <el-input size="small"
                          placeholder="请输入内容"
                          v-model="input"
                          clearable>
                </el-input>
              </div></el-col>
              <el-col :span="6" align="left"><div class="grid-content bg-purple-light">
               &nbsp;&nbsp; <el-button type="primary" size="small" icon="el-icon-search">搜索</el-button>
              </div></el-col>
            </div></el-col>
          </el-row>
        </div></el-col>
      </el-row>

    </el-header>
    <el-main>

    </el-main>
    <el-footer height="150px"></el-footer>
  </el-container>
</template>

<script>
export default {
  name: "SouSuo",
  data(){
    return {
      input:""
    }

  },
  methods:{
    goBack(){
      this.$router.push("/Home")
    }
  }
}
</script>

<style scoped>

</style>



