<template>
  <el-row>
    <el-col :span="24" align="left">
      <span style="color: royalblue;font-size: 22px">Fun</span>
    </el-col>
    <el-col :span="24" align="left">
      <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span style="color: royalblue">幽你一默</span>
        <el-button @click="reFl" style="float: right; padding: 3px 0" type="text"></el-button>
      </div>
      <div v-for="(m,index) in msg" style="padding: 5px 0 " class="text item">
         <span style="color: royalblue">{{index+1}}</span> {{m}}
      </div>
    </el-card>
    </el-col>
  </el-row>

</template>

<script>
import Axios from "axios";

export default {
  name: "News",
  data(){
    return{
      msg:["苏瑜朝又把饭卡丢了","彤彤也把饭卡丢了","鱼寿的梦露"]
    }
  },
  methods:{
    reFl:function (){
      let that =this;
        Axios({
          method:"get",
          url:" https://www.juhe.cn/docs/api/id/58"

        }).then(function (res){
          console.log(res);
        })
    }
  }
}
</script>

<style scoped>

