<template>
    <div class="Center-main">
      <el-page-header @back="goBack" style="padding: 20px 0" content="个人中心">
      </el-page-header>
      <div>
        <el-tabs type="border-card">
          <el-tab-pane label="个人主页">
            <CenterHome></CenterHome>
          </el-tab-pane>
          <el-tab-pane label="上架商品">
            上架商品界面
          </el-tab-pane>
          <el-tab-pane label="愿望">
            愿望界面
          </el-tab-pane>
          <el-tab-pane label="我的足迹">
            历史记录界面
          </el-tab-pane>
          <el-tab-pane label="反馈建议">

          <el-row>
            <el-col :span="8" offset="1">
              <el-input
                type="textarea"
                placeholder="请输入你的意见或建议："
                v-model="textarea"
                maxlength="200"
                show-word-limit
              >
              </el-input>
            </el-col>
            <el-col :span="8" align="center">
              <el-button @click="sumitadvice" >提交</el-button>
            </el-col>
          </el-row>
          <el-row>

          </el-row>
          </el-tab-pane>
          <el-tab-pane label="更改个人信息">
            更改密码界面
          </el-tab-pane>
        </el-tabs>
      </div>

      <el-divider></el-divider>

    </div>
</template>

<script>
import CenterHead from "../components/CenterHead";
import CenterHome from "../components/CenterHome";
import Axios from "axios";
export default {
  name: "Center",
  data(){
    return{
      textarea: ''
    }
  },
  methods:{
    goBack() {
        this.$router.push("/Home");
    },
    sumitadvice(){
      if(this.textarea==''){
        this.$message.error("反馈不能为空")
        return;
      }
      //发ajax存反馈信息
      this.$message.success("你的反馈已经提交到后台")
      this.textarea='';
    }
  },
  components: {CenterHome, CenterHead},
  props:[CenterHead,CenterHome],
  created() {
    this.$store.dispatch('reFashUserInfo');
    if (this.$store.state.resUserInfo.code===false){
        this.$router.push("/Login");
    }
  }
}
</script>

<style scoped>
    .Center-main{

    }
</style>



