// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue';
import App from './App';
import router from './router';
import  Router from 'vue-router';
import Vuex from 'vuex'
//导入el-ul
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
import Top from "./components/Top";
import News from "./components/News";
import History from "./components/History";
import YunShi from "./components/YunShi";
import Header from "./components/Header";
import axios from 'axios'
import VueAxios from "vue-axios";
import  infiniteScroll from "vue-infinite-scroll"
import Axios from "axios";
Vue.use(VueAxios,axios,infiniteScroll);
Vue.use(ElementUI);
Vue.use(Router);

Vue.use(Vuex);
Vue.component("MyHeader",Header);
Vue.component("MyTop",Top);
Vue.component("MyNews",News);
Vue.component("MyHistory",History);
Vue.component("MyYunShi",YunShi);
Vue.config.productionTip = false
/*
axios.defaults.withCredentials = true;
Vue.http.options.credentials = true;
*/

const store = new Vuex.Store({
//  state中存储的就是全局共享数据
      state:{
        resUserInfo:{
          "code": "400",
          "msg": "",
          "userInfo": {
          "bid": "10001",
            "bname": "张三",
            "breal": "200",
            "bfans": "80",
            "bfocus": "56",
            "bpic": "./static/photo/xiandan.png",
            "bscore": "999",
            "bcredit": "4.5",
            "bqianming": "乐观积极向上",
            "lasttime": "2020-11-11 10:15:33",
            "btags": ['A','B','C'],
            "bmsg": "new"
             }
        }
      },
      mutations:{
          textuser(state,step){
            state.resUserInfo=step;
          }
      },
      actions:{
        reFashUserInfo(context){
          Axios({
            method:"post",
            url:"https://localhost:8080/getUserInfo"
          }).then(function (res) {

             context.commit('textuser',res.data.data);
          })
        }
      },
      getters:{

      }
})

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: {
    App
     },
  template: '<App/>',
  store
})



